.about__wrp {
  margin: 0;
  height: 140vh;
  position: relative;
}

* {
  box-sizing: border-box;
}

.container {
  margin-top: 3%;
  width: 100%;
  background: rgba(182, 81, 81, 0.15);
  border: 2px solid #190d0d;
  border-radius: 0 20px 20px 0;
}

.skills {
  text-align: center;
  color: #b24c4c;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.skills__wrp {
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-around;
}
.skills {
  position: relative;
}
.skills::before {
  content: '';
  position: absolute;
  background-color: #1a4f52;
  z-index: 1;
  height: 100%;
  left: 0;
  animation: wipe 0.6s 0.5s cubic-bezier(0.2, 0.4, 0.6, 0.4) forwards;
}
@keyframes wipe {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
.skills__text {
  font-size: 15px;
  color: #b45050;
  padding: 7px;
  margin: 0;
  background: #67347040;
  width: 150px;
  border: 2px solid #d74949;
  z-index: 2;
}
.skills__span {
  align-self: center;
  margin-right: 15px;
  z-index: 2;
}
.skills__list {
  width: 90%;
  margin: 30px 5% 20px;
}

.html {
  width: 90%;
}
.css {
  width: 90%;
}
.react {
  width: 90%;
}
.js {
  width: 75%;
}
.routerRedux {
  width: 70%;
}

.about {
  margin: 0px 0px 15px 0px;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  background: linear-gradient(
    183deg,
    rgb(1, 2, 19) 23%,
    rgba(13, 0, 36, 0.8970938717) 40%,
    rgb(17, 7, 36) 100%
  );
  text-transform: uppercase;
  font-size: 20px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px -50px 36px -28px inset;
  border-bottom: 5px solid #3a3865;
}
.features {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: center;
}
.aboutSkillsWrp {
  display: flex;
}
.aboutCard {
  padding: 15px 0 0 10px;
  margin: 25px 20px 0 15px;
  width: 400px;
  height: 150px;
  display: grid;
  grid-template-rows: 20px 50px 1fr 50px;
  border-radius: 10px;
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
  transition: all 0.2s;
  align-items: center;
  justify-items: start;
  transition: 0.5s;
}

.card1,
.card3 {
  background: radial-gradient(#129a76, #1d3746);
}
.card2 {
  background: radial-gradient(#bd5b5b, #9e3232);
}
.about__title {
  margin-top: 70px;
  color: #96b1aa;
}
.cardText {
  padding: 20px 25px 0;
  color: #0d0c2f;
  font-size: 21px;
}
.about__text {
  color: #8e7474;
  width: 98%;
  padding: 15px 15px 25px;
}

.custom__shape__divider__top__1657712595 {
  position: absolute;
  top: -1vh;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
}

.custom__shape__divider__top__1657712595 svg {
  position: relative;
  display: block;
  width: calc(300% + 1.3px);
  height: 104px;
}

.custom__shape__divider__top__1657712595 .shape__fill {
  fill: #010213;
}

.custom__shape__divider__bottom__1658322486 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
}

.custom__shape__divider__bottom__1658322486 svg {
  position: relative;
  display: block;
  width: calc(116% + 1.3px);
  height: 22px;
  transform: rotateY(180deg);
}

.custom__shape__divider__bottom__1658322486 .shape__fill {
  fill: #2f2033;
}
.linkWrp {
  display: flex;
  position: absolute;
  top: 135vh;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  transform: translateX(-50%);
}
.contact__link {
  padding: 0px 8px;
  margin: 0 auto;
  border: none;
  border-radius: 0;
  background: none;
  color: #b95555;
  transition: all 0.5s;
  position: relative;
}
.contact__link::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: rgb(4 3 3 / 18%);
  transition: all 0.3s;
  border: 1px solid;
}
.contact__link:hover::before {
  opacity: 0;
  transform: scale(0.5, 0.5);
}
.contact__text {
  transition: all 0.4s;
}
.contact__link:hover .contact__text {
  color: #177464;
}
.contact__link::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  transition: all 0.5s;
  border: 1px solid rgb(26 130 130 / 50%);
  transform: scale(1.2, 1.2);
}
.contact__link:hover::after {
  opacity: 1;
  transform: scale(1, 1);
}
.contact__link:hover i {
  transform: rotate(90deg);
  color: #177464;
}

@media screen and (max-width: 1024px) {
  .about__wrp {
    min-height: 100vh;
  }
  .about__title {
    font-size: 21px;
  }
  .about__text {
    font-size: 16px;
    width: 80%;
    text-align: center;
  }
  .about__text__wrp {
    display: flex;
    justify-content: center;
  }
  .aboutCard {
    width: 250px;
    height: 120px;
  }
  .cardText {
    padding: 13px 15px 0;
    color: #0d0c2f;
    font-size: 15px;
  }
  .cardTitle {
    font-size: 20px;
  }

  .container {
    margin-top: 3%;
  }
  .skills__text {
    font-size: 14px;
    padding: 3px;
  }
  .skills__span {
    font-size: 14px;
  }
  .linkWrp {
    top: 115vh;
  }
}

@media screen and (max-width: 768px) {
  .linkWrp {
    top: 105vh;
  }
  .about__title {
    font-size: 20px;
  }
  .about__text {
    font-size: 15px;
    width: 80%;
    text-align: center;
  }
  .about__text__wrp {
    display: flex;
    justify-content: center;
  }
  .aboutCard {
    width: 240px;
    height: 120px;
  }
  .cardText {
    padding: 11px 15px 0;
    color: #0d0c2f;
    font-size: 14px;
  }
  .cardTitle {
    font-size: 19px;
  }

  .container {
    margin-top: 2%;
  }
  .skills__text {
    font-size: 13px;
    padding: 2px;
  }
  .skills__span {
    font-size: 13px;
  }
}

@media (max-width: 625px) {
  .features {
    flex-direction: column;
    align-items: center;
  }
  .aboutCard {
    width: 50%;
    height: 80px;
    text-align: center;
    margin: 10px 0 0;
  }
  .cardText {
    padding: 0px 5px 0;
    font-size: 12px;
  }
  .skills__list {
    margin: 15px 5% 15px;
  }
  .skills__text {
    width: 100px;
  }
  .container {
    margin-top: 5px;
  }
  .linkWrp {
    display: none;
  }
  .about__title {
    font-size: 20px;
  }
  .about__text {
    font-size: 12px;
  }
}
@media screen and (max-height: 800px) {
  .about__wrp {
    max-height: 200vh;
  }
  .linkWrp {
    display: none;
  }
}
@media screen and (max-height: 500px) {
  .about__wrp {
    min-height: 170vh;
  }
}

@media screen and (min-height: 750px) and (max-width: 600px) {
  .linkWrp {
    display: flex;
    top: 120vh;
  }
  .aboutCard {
    width: 50%;
    height: 120px;
    text-align: center;
    margin: 10px 0 0;
  }
  .cardText {
    padding: 0px 5px 0;
    font-size: 15px;
  }
  .container {
    margin-top: 4%;
  }
} ;
